import { spawn } from "child_process";
import path from "path";

export async function POST(req) {
  try {
    const body = await req.json();
    const { data, future_years } = body;

    const pythonPath = "C:\\Users\\ALDEGS09\\AppData\\Local\\Programs\\Python\\Python313\\python.exe";

    const scriptPath = path.join(process.cwd(), "app/forecast/model.py");

    const py = spawn(pythonPath, [scriptPath]);

    py.stdin.write(JSON.stringify({ data, future_years }));
    py.stdin.end();

    let result = "";

    py.stdout.on("data", (chunk) => {
      result += chunk.toString();
    });

    return await new Promise((resolve) => {
      py.on("close", () => {
        try {
          resolve(new Response(result, { status: 200 }));
        } catch (err) {
          resolve(new Response(JSON.stringify({ error: err.toString() }), { status: 500 }));
        }
      });
    });

  } catch (err) {
    return new Response(JSON.stringify({ error: err.toString() }), { status: 500 });
  }
}
